
.clearfix::after {
  display: block;
  content: '';
  clear: both;
}
.cardContent {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  padding-bottom: 20px;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.cardContent.move-enter-active, .cardContent.move-leave-active {
    transition: all .2s linear;
}
.cardContent.move-enter, .cardContent.move-leave-active {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
.cardContent .cardContent-header {
    width: 100%;
    height: 50px;
    line-height: 50px;
    position: relative;
    text-align: center;
}
.cardContent .cardContent-header:after {
      display: block;
      position: absolute;
      left: 0;
      width: 100%;
      border-top: 1px solid #e6e6e6;
      content: '';
}
.cardContent .cardContent-header:after {
      bottom: 0;
}
.cardContent .cardContent-header:before {
      top: 0;
}
.cardContent .cardContent-header .icon-wrapper {
      display: inline-block;
      text-align: center;
      margin-left: 10px;
      float: left;
}
.cardContent .cardContent-header .icon-wrapper i {
        font-size: 19px;
        border: 2px solid gray;
        border-radius: 50%;
        padding: 5px;
}
.cardContent .cardContent-header .word-wrapper {
      display: inline-block;
      margin-right: 30px;
      font-size: 20px;
}
.cardContent .load-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate3d(-40%, -60%, 0);
            transform: translate3d(-40%, -60%, 0);
}
.cardContent .load-wrapper p {
      font-weight: bold;
      font-size: 16px;
}
.cardContent .content-wrapper {
    position: absolute;
    top: 53px;
    left: 0;
    bottom: 63px;
    width: 100%;
    overflow: hidden;
}
.cardContent .content-wrapper .public-wrapper {
      position: relative;
      padding-bottom: 3px;
      margin-top: 5px;
      border-bottom: 1px solid #e6e6e6;
}
.cardContent .content-wrapper .public-wrapper .avatar-wrapper {
        display: inline-block;
        margin: 10px 3px 10px 10px;
}
.cardContent .content-wrapper .public-wrapper .avatar-wrapper img {
          width: 40px;
          height: 40px;
          border-radius: 50%;
}
.cardContent .content-wrapper .public-wrapper .name-wrapper {
        display: inline-block;
        position: relative;
        top: -10px;
}
.cardContent .content-wrapper .public-wrapper .name-wrapper h2 {
          color: #80bd01;
}
.cardContent .content-wrapper .public-wrapper .time-wrapper {
        display: inline-block;
        position: absolute;
        top: 30px;
        left: 55px;
}
.cardContent .content-wrapper .public-wrapper .time-wrapper span {
          color: gray;
}
.cardContent .content-wrapper .public-wrapper .person-title {
        float: right;
        margin: 10px;
}
.cardContent .content-wrapper .public-wrapper .person-title span {
          color: gray;
}
.cardContent .content-wrapper .title-wrapper {
      text-align: center;
      border-bottom: 1px solid #e6e6e6;
}
.cardContent .content-wrapper .title-wrapper .title {
        margin-top: 10px;
        text-align: center;
        font-size: 22px;
        line-height: 25px;
        font-weight: bold;
}
.cardContent .content-wrapper .title-wrapper .readcount-wrapper, .cardContent .content-wrapper .title-wrapper .replycount-wrapper {
        margin: 10px 0;
}
.cardContent .content-wrapper .title-wrapper .readcount-wrapper {
        display: inline-block;
}
.cardContent .content-wrapper .title-wrapper .readcount-wrapper i {
          font-size: 20px;
          vertical-align: top;
}
.cardContent .content-wrapper .title-wrapper .readcount-wrapper span {
          font-size: 13px;
          vertical-align: top;
}
.cardContent .content-wrapper .title-wrapper .replycount-wrapper {
        display: inline-block;
}
.cardContent .content-wrapper .title-wrapper .replycount-wrapper i {
          font-size: 20px;
          vertical-align: top;
}
.cardContent .content-wrapper .title-wrapper .replycount-wrapper span {
          font-size: 13px;
          vertical-align: top;
}
.cardContent .content-wrapper .html-wrapper {
      padding: 10px;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > h1 {
        padding-bottom: 10px;
        font-size: 28px;
        line-height: 25px;
        border-bottom: 1px solid #e6e6e6;
        font-weight: bold;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > h2 {
        padding: 10px 0 10px 0;
        font-size: 23px;
        line-height: 25px;
        border-bottom: 1px solid #e6e6e6;
        font-weight: bold;
        margin-bottom: 10px;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > h3 {
        padding: 10px 0 10px 0;
        font-size: 19px;
        line-height: 25px;
        border-bottom: 1px solid #e6e6e6;
        font-weight: bold;
        margin-bottom: 10px;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > p {
        text-align: justify;
        font-size: 16px;
        margin-bottom: 10px;
        line-height: 25px;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > p > img {
          height: 250px;
          width: 100%;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > p > a {
          color: blue;
          text-decoration: none;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > p > strong {
          font-weight: bold;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > ul {
        padding-left: 10px;
        margin-bottom: 15px;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > ul li {
          margin-top: 8px;
          font-size: 15px;
          line-height: 25px;
          text-align: justify;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > ul li a {
            color: blue;
            text-decoration: none;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > blockquote {
        margin: 25px 0;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > blockquote > p {
          padding-left: 5px;
          font-size: 16px;
          color: #6a737d;
          border-left: 0.25em solid #dfe2e5;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > blockquote > p a {
            display: block;
            color: blue;
            text-decoration: none;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > blockquote > ul li a {
          color: blue;
          text-decoration: none;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > table {
        overflow: auto;
        display: block;
        width: 100%;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > table thead tr {
          background: #fff;
          border-top: 1px solid #c6cbd1;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > table thead tr th {
            padding: 6px 13px;
            border: 1px solid #dfe2e5;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > table tbody tr {
          background-color: #fff;
          border-top: 1px solid #c6cbd1;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > table tbody tr td {
            padding: 6px 13px;
            border: 1px solid #dfe2e5;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > pre {
        padding: 16px;
        overflow: auto;
        font-size: 85%;
        line-height: 1.45;
        background-color: #f6f8fa;
        border-radius: 3px;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > pre code {
          color: black;
          display: inline;
          max-width: auto;
          padding: 0;
          margin: 0;
          overflow: visible;
          line-height: inherit;
          word-wrap: normal;
          background-color: transparent;
          border: 0;
}
.cardContent .content-wrapper .html-wrapper .markdown-text > pre code {
        font-size: 14px;
}
.cardContent .reply-wrapper .count-wrapper {
    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
    border-left: 5px solid #80bd01;
    padding: 7px 0 7px 5px;
}
.cardContent .reply-wrapper ul {
    padding-left: 0;
}
.cardContent .reply-wrapper .replyPerson-wrapper {
    margin-top: 10px;
    position: relative;
    border-bottom: 1px solid #e6e6e6;
    list-style: none;
}
.cardContent .reply-wrapper .replyPerson-wrapper .floor {
      float: right;
      margin: 6px 10px 0 0;
      color: #80bd01;
}
.cardContent .reply-wrapper .replyPerson-wrapper .avatar-wrapper {
      margin: 23px 5px 5px 10px;
      display: inline-block;
}
.cardContent .reply-wrapper .replyPerson-wrapper .avatar-wrapper img {
        width: 30px;
        height: 30px;
        border-radius: 50%;
}
.cardContent .reply-wrapper .replyPerson-wrapper .name-wrapper {
      display: inline-block;
      position: relative;
      top: -7px;
}
.cardContent .reply-wrapper .replyPerson-wrapper .name-wrapper span {
        font-weight: bold;
}
.cardContent .reply-wrapper .replyPerson-wrapper .time-wrapper {
      display: inline-block;
      position: absolute;
      top: 39px;
      left: 47px;
}
.cardContent .reply-wrapper .replyPerson-wrapper .time-wrapper span {
        color: gray;
}
.cardContent .reply-wrapper .replyPerson-wrapper .markdown-text {
      padding: 3px 3px 3px 47px;
}
.cardContent .reply-wrapper .replyPerson-wrapper .markdown-text p a {
        color: blue;
        text-decoration: none;
}
.cardContent .reply-wrapper .replyPerson-wrapper .markdown-text p img {
        height: 150px;
        width: 100%;
}
.cardContent .reply-wrapper .replyPerson-wrapper .icon-wrapper {
      float: right;
      margin: 0 10px 10px 0;
}
.cardContent .reply-wrapper .replyPerson-wrapper .icon-wrapper .praise-wrapper {
        display: inline-block;
        width: 32px;
}
.cardContent .reply-wrapper .replyPerson-wrapper .icon-wrapper .praise-wrapper i {
          color: gray;
}
.cardContent .reply-wrapper .replyPerson-wrapper .icon-wrapper .praise-wrapper .num {
          color: gray;
}
.cardContent .reply-wrapper .replyPerson-wrapper .icon-wrapper .used-wrapper {
        display: inline-block;
}
.cardContent .reply-wrapper .replyPerson-wrapper .icon-wrapper .used-wrapper i {
          color: gray;
}
.cardContent .reply-wrapper .login-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    border-top: 1px solid #e6e6e6;
    width: 100%;
    height: 200px;
}
.cardContent .reply-wrapper .login-wrapper .login {
      font-size: 15px;
}
.cardContent .reply-wrapper .login-wrapper .login a {
        color: #80bd01;
}
.cardContent .reply-wrapper .login-wrapper .public {
      font-size: 15px;
}
.cardContent .reply-wrapper .login-wrapper .public a {
        color: #80bd01;
}



