@mixin border-1px($color) {
    position: relative;
    &:after {
        display: block;
        position: absolute;
        left: 0;
        width: 100%;
        border-top: 1px solid $color;
        content: '';
    }
    &:after {
        bottom: 0;
    }
    &:before {
        top: 0;
    }
} //处理移动端1px
@mixin border-none() {
    &:after {
        display: none;
    }
}

@mixin bg-image($url) {
    background-image: url($url+"@2x.png");
    @media(-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
        background-image: url($url + "@3x.png");
    }
} //处理不同分辨率的终端的背景图
.clearfix::after {
    display: block;
    content: '';
    clear: both;
}


